.home-container {
  text-align: center;
  margin-top: 50px;
}

.home-heading {
  font-size: 2em;
  margin-bottom: 20px;
}

.home-paragraph {
  font-size: 1.2em;
  margin-bottom: 30px;
}

.home-links-container {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.home-link {
  font-size: 1.2em;
  color: blue;
  text-decoration: none;
  padding: 10px 20px;
  border: 1px solid blue;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.home-link:hover {
  background-color: blue;
  color: white;
}
